<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div id="divMainLeadData">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('RequestEmployee') }}</span>                       
                        <span class="float-right">
                            <a href="javascript:;" class="p-action-btn text-white" :title="$t('BacktoList')"
                                @click="Close">
                                <em class="fa fa-angle-double-left pr-2" aria-hidden="true"></em>
                                <br />{{ $t('BacktoList') }}
                            </a>
                        </span>
                    </div>
                </div>
            </div>
            <div class="border p-3">
                <Form ref="form">
                    <dynamic-form ref="RequestEmployeeForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" :mandatorytext='MandatoryString'>
                    </dynamic-form>
                </Form>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
export default {
    data(){
        return{
            isLoading:false,
            FormSchema: [
            {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "RadioField",
                            label: this.$t('CompanyType'),
                            name: "CompanyType",
                            value: "Resource",
                            placeholder: "",
                            config: {
                                options: [{
                                    name: 'Company', value: 'Resource'
                                }, {
                                    name: "Vendor", value: 'Service'
                                }],
                                onChange:this.LoadCompanies,
                                showAddIcon: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Linkedcompany'),
                            name: "Linkedcompany",
                            value: "",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onChange:this.ddlLinkedCompany,
                            },
                            placeholder: this.$t('Select Linked Company'),
                            validationRules: { "required": true},
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('RequestType'),
                            name: "RequestType",
                            value: "",
                            placeholder: "",
                            visibility:false,
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('ShareYourLicenseMessage'),
                            name: "ShareYourLicenseMessage",
                            value: "Resource",
                            placeholder: "",
                            visibility:false,
                            config: {
                                options: [{
                                    name: 'Yes', value: 'Yes'
                                }, {
                                    name: "No", value: 'No'
                                }],
                                showAddIcon: false
                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Project'),
                            name: "Project",
                            value: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange:this.ddlProject
                            },
                            placeholder: this.$t('Select Project'),
                            validationRules: { "required": true},
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('SelectService'),
                            name: "SelectService",
                            visibility:false,
                            value: "",
                            config: {
                                options: [],
                                showAddIcon: false
                            },
                            placeholder: this.$t('Select Service'),
                            validationRules: { "required": true},
                        }
                    ]
                },
                {     
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            visibility:false,
                            validationRules: "",                
                        }
                    ]         
                }
            ],
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Send Request",
                    isVisible:false
                }               
            ],
            vendorId:null,
            projectId:null

        }
    },
    created:function(){
        this.GetRequestData();
        this.GetLinkedCompanyDetail();
        this.GetProjectList();
    },
    methods:{
        GetRequestData:function(){
            var ths=this;
            var url=`RequestID=`;
            DataService.GetRequestData(url).then((response)=>{
                debugger
            })
        },
        GetProjectList:function(){
            var ths=this;
            DataService.GetProjectList().then((response)=>{  
                if(response.data.result.length>0){
                    var ProjectData=response.data.result;
                    ProjectData.forEach((element,index) => {
                        var obj={
                            name:element.project_name,
                            value:element.project_id
                        }                        
                        ths.FormSchema[0].Data[4].config.options.push(obj);
                    });                    
                    ths.$refs.RequestEmployeeForm.UpdateKeyValue();
                 }
            })
        },
        GetLinkedCompanyDetail:function(){            
            var ths=this;  
            var url=`UserType=Company`
            DataService.GetLinkedCompanyDetail(url).then((response)=>{                
                if(response.data.result.length>0){                    
                    var LinkCompanyData=response.data.result;
                    LinkCompanyData.forEach((element,index) => {
                        var obj={
                            name:element.COMPANY_NAME,
                            value:element.RELATED_COMPANY_ID
                        }                        
                        ths.FormSchema[0].Data[1].config.options.push(obj);
                    });                    
                    ths.$refs.RequestEmployeeForm.UpdateKeyValue();
                }
            })
        },
        LoadCompanies:function(x,y,z){ 
            var ths=this;  
            ths.isLoading=true;
            ths.FormSchema[0].Data[1].value='';                      
            ths.FormSchema[0].Data[1].config.options=[];
            var UserType = y.value=='Resource'?'Company':'Vendor';
            var url=`UserType=${UserType}`
            ths.FormSchema[0].Data[2].visibility=false;
            ths.FormSchema[0].Data[5].visibility=false;
            ths.FormSchema[1].Data[0].visibility=false;
            if(UserType=='Vendor'){
                ths.buttons[0].isVisible=true;
            } else{
                ths.buttons[0].isVisible=false;
            }           
            DataService.GetLinkedCompanyDetail(url).then((response)=>{                  
                if(response.data.result.length>0){                    
                    var LinkCompanyData=response.data.result;
                    LinkCompanyData.forEach((element,index) => {
                        var obj={
                            name:element.COMPANY_NAME,
                            value:element.RELATED_COMPANY_ID
                        }                        
                        ths.FormSchema[0].Data[1].config.options.push(obj);
                    });                    
                    ths.$refs.RequestEmployeeForm.UpdateKeyValue();
                    ths.isLoading=false; 
                }
            })
        },
        ddlLinkedCompany:function(x,y,z){ 
            var ths=this;
            ths.vendorId=y.value;
            if(ths.FormSchema[0].Data[0].value=='Resource'){                
                var resource=[{name: 'Resource', value: 'Resource'}];
                ths.FormSchema[0].Data[2].visibility=true;
                ths.FormSchema[0].Data[5].visibility=true;
                ths.FormSchema[1].Data[0].visibility=true;
                ths.FormSchema[0].Data[2].config.options=resource;
            }
            if(ths.FormSchema[0].Data[0].value=='Service'){                
                var resource=[{name: 'Resource', value: 'Resource'},{name: "Service", value: 'Service'}];
                ths.FormSchema[0].Data[2].visibility=true;
                ths.FormSchema[0].Data[5].visibility=true;
                ths.FormSchema[1].Data[0].visibility=true;
                ths.FormSchema[0].Data[2].config.options=resource;
                
            }
            ths.BindServiceOnProjectChange();
            
        },
        ddlProject:function(x,y,z){ 
            var ths=this
            ths.projectId=y.value;
            ths.BindServiceOnProjectChange()
        },
        BindServiceOnProjectChange:function(){            
            var ths=this
            var url=`vendorId=${ths.vendorId}&projectId=${ths.projectId}`
            DataService.BindVendorServicesByVendorWise(url).then((response)=>{  
                debugger
             }) 
        },
        async onSubmit(formfields, val) {     

        },
        Close:function(){
            this.$router.push({ name: 'EmployeeSharingList' });
        }
    }
}
</script>